import { Component, OnInit, Injectable} from '@angular/core';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {

  loaded = true;
  txt="Hello team, I need to create a complete website for our business. It's salon. It should be responsive and medern one. ";
  requirement = "";
  activeTestaminal = 1;
  requirementBucket = false;


  constructor() { 
    setTimeout(() => {
      this.loaded = true;
    }, 2000);
    
    var i=0;
    setInterval(() => {
      this.requirement = this.txt.slice(0,i);
        i++;
      if(i==150)
        i=0
    }, 100); 

    var j=1;
    setInterval(() => {
      this.activeTestaminal = j;
        j++;
      if(j==3)
        j=1
    }, 5000); 

  }
  
  ngOnInit(): void {
  }

  focusReqBucket(){
    this.requirementBucket=!this.requirementBucket;
  }

}
